body {
  color: #333333;
}
a {
  color: #333333;
  text-decoration: none;
}
a:hover {
  color: #3b5998;
}
.app-wrapper {
  margin: 0 auto;
  width: 1200px;
  display: grid;
  grid-template-areas:
    "h h"
    "n c ";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
}
